.drawingList{
    display: flex;
    flex-wrap:wrap;
    justify-content: center;    
    position: absolute;
    z-index: 100;
    width: 100vw;
    border-bottom: 1px solid #dcdcdc;
}

.dd{
    min-width: 100px;
    display: flex;
    justify-content: center;
}

.ddCkOn >div{
    width: fit-content;
    font-weight: 700;
    border-bottom: 3px solid #323232;
    padding-top:15px;
    padding-bottom:10px;
    margin:0px 5px 0px 5px;
}
.ddCkOff >div{
    width: fit-content;
    color: #969696;
    font-weight: 500;
    border-bottom: 3px solid #00000000;
    padding-top:15px;
    padding-bottom:10px;
    margin:0px 5px 0px 5px;
}

.imgBox{
    position: absolute;
    z-index: 10;
}